import React, { useState } from 'react';
import { Grid, Button, Text, Header } from '../../../style';
import { Input, DatePicker, Switch, InputNumber, Select } from 'antd';
import { useFormik } from 'formik';
import { moment, FORMAT_DATETIME, Moment } from '../../../utils/time';
import Label from '../../../components/label';
import logger from '../../../utils/logger';
import { Event } from '../type';
import { Garage } from '../../../model/domain/garage';
import useGarages from '../../../hooks/garage';
import { disabledTxtColor } from '../../../config';

const nowISO = moment().toISOString();

const defaultValues = () : Event.Event=> ({
    id: String(Math.random() * 10000),
    name: "",
    status: 'active',
    location: "",
    type: "paid",
    startTime: new Date().toISOString(),
    endTime: new Date().toISOString(),
    launchAt: nowISO,
    garages: [defaultGarage],
});

const defaultGarage : Event.Garage = {
    id: '',
    price: 0,
    staffOccupancy: 0,
    maxOccupancy: 0
}
const format = FORMAT_DATETIME;
const Form = ({ add, close, initialValues = defaultValues(), eId }: any) => {
    const { garages } = useGarages({ eId });
    const [loading, setLoading] = useState(false);
    const { handleSubmit, handleChange, values, setFieldValue } = useFormik({
        initialValues,
        onSubmit(values) {
            submit(values)
        }
    })
    const submit = async (values: any) => {
        try {
            setLoading(true)
            await add(values);
            close();
        } catch (e) {
            logger.log(e)
        } finally {
            setLoading(false)
        }
    }

    const error = !values.name || !values.location
    const style = {
        padding: ".5em 1em", height: "100%", width: "100%",
        placeItems: "start stretch", gridGap: ".4em", background: "white"
    }

    return (
        <>
        <Header bold textAlign='left'>Event Information</Header>
    <form onSubmit={handleSubmit} className="grid" style={{ 
    ...style, gridGap: '1em', maxWidth: '500px',
     overflowY: 'auto' }}>
     
        <div>
            <Text textAlign="left" bold>Name<span className='red'>*</span></Text>
            <Input
                name={"name"}
                value={values.name}
                onChange={handleChange}
                placeholder="Example: SJSU Graduation Ceramony"
            />
        </div>

        <div>
            <Text textAlign="left" bold>Location <span className='red'>*</span></Text>
            <Input
                name={"location"}
                value={values.location}
                onChange={handleChange}
                placeholder="Example: 3300 Madison St"
            />
        </div>

        <div className="grid" style={{ placeItems: "end" }}>
            <Text textAlign="left" placeSelf="stretch" bold>
                Start Time
                <Label label={'Date on which the event will start'} />
            </Text>
            <DatePicker
                use12Hours
                showTime={{ format: 'hh:mm a' }}
                format={format}
                minuteStep={15}
                style={{ width: "100%" }}
                value={moment(values.startTime)}
                onChange={(e: Moment | null) => {
                    if (!e) return;
                    setFieldValue("startTime", e.toISOString());
                }} />
        </div>

        <div className="grid" style={{ placeItems: "end" }}>
            <Text textAlign="left" placeSelf="stretch" bold>
                End Time
                <Label label={'Date on which the event will end'} />
            </Text>
            <DatePicker
                use12Hours
                showTime={{ format: 'hh:mm a' }}
                format={format}
                minuteStep={15}
                style={{ width: "100%" }}
                value={moment(values.endTime)}
                onChange={(e: Moment | null) => {
                    if (!e) return;
                    setFieldValue("endTime", e.toISOString());
                }} />
        </div>

        <div className="grid" style={{ placeItems: "end" }}>
            <Text textAlign="left" placeSelf="stretch" bold>
                Launch Date
                <Label label={'Date on which the message will appear in ParkStash App'} />
            </Text>
            <DatePicker
                use12Hours
                showTime={{ format: 'hh:mm a' }}
                format={format}
                minuteStep={15}
                style={{ width: "100%" }}
                value={moment(values.launchAt)}
                onChange={(e: Moment | null) => {
                    if (!e) return;
                    setFieldValue("launchAt", e.toISOString());
                }} />
        </div>

        <Grid margin=".6em 0" placeSelf="stretch" placeItems="stretch" gridGap="0" customCols="1fr auto">
                <Text textAlign="left" placeSelf="end start" bold>
                    Paid Event 
                </Text>
                <Switch
                    checked={values.type === 'paid'}
                    style={{ placeSelf: "end" }}
                    onChange={(checked: boolean) => 
                        setFieldValue("type", checked? 'paid': 'free')
                    }
                />
        </Grid>
        <Grid margin=".6em 0" placeSelf="stretch" placeItems="stretch" gridGap="0"  customCols='1fr auto'>
                <Text textAlign="left" placeSelf="end start">
                <span className='bold'>Add a Garages:</span> Garages ({values.garages.length})
                </Text>
                <i className='fa fa-plus pointer' onClick={()=>setFieldValue("garages", [...values.garages, defaultGarage])}></i>
                {values.garages.map(({}: Event.Garage, i: number)=>(
                <Grid margin=".6em 0" placeSelf="stretch" placeItems="stretch" customCols='auto 1fr' style={{gridColumn: '1/-1'}}>
              
                    <Text style={{gridRow: 'span 5'}}>{i + 1}.</Text> 

                    <Select
                    style={{width: "100%"}}
                   // value={values.garages[i].id}
                //    onChange={(e) =>setFieldValue('garages', e)}
                >
                {garages.map(({ name, id }: Garage) => <Select.Option value={id}>{name}</Select.Option>)}
                </Select>
                    {values.type === 'paid' && 
                    <Grid margin=".6em 0" placeSelf="stretch" placeItems="stretch" gridGap="0" customCols="1fr auto">
                      <Text textAlign="left" placeSelf="end start" bold>Price ($)</Text>
                           <InputNumber
                               min={0}
                               step={0.25}
                               style={{ placeSelf: "end" }}
                           //    value={values.garages[i].price}
                        //       onChange={(e) => setFieldValue("price", e)}
                               name={"price"}
                           />
                   </Grid>}

                   <Grid margin=".6em 0" placeSelf="stretch" placeItems="stretch" gridGap="0" customCols="1fr auto">
                       <Text textAlign="left" placeSelf="end start" bold>Max Occupancy: </Text>
                           <InputNumber
                               min={0}
                               style={{ placeSelf: "end" }}
                           //    value={values.garages[i].maxOccupancy}
                         //      onChange={(e) => setFieldValue("maxOccupancy", e)}
                               name={"maxOccupancy"}
                           />
                   </Grid>
                   <Grid margin=".6em 0" placeSelf="stretch" placeItems="stretch" gridGap="0" customCols="1fr auto">
                       <Text textAlign="left" placeSelf="end start" bold>Staffs: </Text>
                           <InputNumber
                               min={0}
                               style={{ placeSelf: "end" }}
                             //  value={values.garages[i].staffOccupancy}
                           //    onChange={(e) => setFieldValue("staffOccupancy", e)}
                               name={"staffOccupancy"}
                           />
                   </Grid>
                   <Text 
                   textAlign='right' 
                   className='pointer' 
                   margin='0 0 2em 0'
                   bold
                   onClick={()=>setFieldValue('garages', values.garages.filter((_: any, index: number)=> index !== i))}
                   >
                       Remove this Garage
                   </Text>
                </Grid>))}
        </Grid>
 
        <Grid cols="2" size="0.7em" placeItems={"stretch"} style={{marginBottom: '5em'}}>
            <Button.Normal
                type="submit"
                disabled={loading}
                onClick={() => close()}
            >
                Cancel
                </Button.Normal>
            {loading ?
                <Button loading disabled>Loading</Button> :
                <Button.Normal
                    type="submit"
                    disabled={error}
                    onSubmit={(e: any) => handleSubmit(e)}>
                    Confirm
            </Button.Normal>
            }
        </Grid>
    </form>
    </>
    )
}

export default Form;
