import { defaultInterval, yearToDate, getInterval } from "../../../utils/time";

const json = {
   "timeframes": [
      {
         "label": "Custom",
         "enteries": [
            {
               "value": null,
               "label": "Custom"
            }
         ]
      },
      {
         "label": "To-Date",
         "enteries": [
            {
               "value": defaultInterval(),
               "label": "Today",
               "defaultValue": true
            },
            {
               "value": yearToDate(),
               "label": "Year To Date"
            },
         ]
      },
      {
         "label": "Interval",
         "enteries": [
            {
               "value": getInterval(7),
               "label": "Last 1 Week"
            },
            {
               "value": getInterval(30),
               "label": "Last 1 Month"
            },
            {
               "value": getInterval(90),
               "label": "Last 3 Months"
            },
            {
               "value": getInterval(365),
               "label": "Last 1 Year"
            },
         ]
      }
   ],
   "aggregations":  {
      "label": "Max/Min Occupancy Selection",
      "values": [
      {
         "value": null,
         "label": "None"
      },
      {
         "label": "Hourly Max",
         "timeSelection" : true,
         "value": {
            "alignmentReducer": "MAX",
            "alignmentPeriod": "1d"
         }
      },
      {
         "label": "Daily Max",
         "timeSelection" : false,  
         "value": {
            "alignmentReducer": "MAX",
            "alignmentPeriod": "1d",
         }
      },
      {
         "label": "Hourly Min",
         "timeSelection" : true,
         "value": {
            "alignmentReducer": "MIN",
            "alignmentPeriod": "1d"
         }
      },
      {
         "label": "Daily Min",
         "timeSelection" : false, 
         "value": {
            "alignmentReducer": "MIN",
            "alignmentPeriod": "1d"
         }
      }
   ]}
}

export default json;