import React, { useState, useEffect } from "react";
import Layout from '../../../layout';
import img from '../../../assets/eventss.svg';
import { Grid, Modal, Switch, Text } from "../../../style";
import Table from "./table";
import Header from "./header";
import { useSelector } from "react-redux";
import Form from "./form";
import { useRefresh } from "../../../hooks/refresh";
import { useEvents } from "../../../hooks/events";
import { Event } from "../type";
import { Overlay } from 'platyplex_ui';
import { disabledTxtColor } from "../../../config";

const states = {
    ACTIVE: "ACTIVE",
    ALL: "ALL",
    EXPIRED: "EXPIRED",
    DEFAULT: "ACTIVE",
    states: ["ACTIVE", "ALL", "EXPIRED"],
};

const Listings = ({ switches }: { switches: JSX.Element }) => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const [state, setState] = useState({ active: states.DEFAULT });
    const { needsRefresh, refresh } = useRefresh();
    const { events, actions } = useEvents({ eId, refresh })
    const AddModal = Modal({
        title: "Add Event",
        className: 'hide',
        modal: <Adder add={actions.addEvent} close={() => AddModal.close()} eId={eId}/>,
        hideCancel: true,
    });

    const open = (selected:Event.Event) => {
        const EditModal = Modal({
            title: "Edit Event",
            modal: <Adder
                eId={eId}
                add={actions.editEvent}
                initialValues={selected}
                close={() => EditModal.close()}
            />,
            hideCancel: true,
        });
        EditModal.open();
    }

    const currentEvents = events.filter(
        ({ status }: Event.Event) =>
            status.toUpperCase() === state.active || state.active === states.ALL
    );

    useEffect(() => {
        //  if (eId) dispatch(getAnnouncements(eId));
    }, [eId]);

    return (
        <Layout sidebar>
            <Layout.Top >
                {switches}
                <Header />
            </Layout.Top>
            <Layout.Bottom>
                <Table
                    className='min-height-table'
                    edit={open}
                    add={AddModal.open}
                    remove={actions.removeEvent}
                    toggler={
                        <Switch
                            style={{ margin: "0 0 .5em 0" }}
                            active={state.active}
                            items={states.states}
                            onClick={(active: string) => setState({ ...state, active })}
                        />
                    }
                    announcements={currentEvents}
                />
            </Layout.Bottom>
        </Layout>
    );
};

const Adder = (props: any) => {
    return (
        <Overlay image={img} className='opacity'>
            <Text
                color={disabledTxtColor}
                margin='.5em'
                placeSelf='start'
                bold
                textAlign='left'
                onClick={props.close}
                className='pointer'
            >
                <i className='fa fa-chevron-left'></i> Back
            </Text>

            <Grid  height='100%' customRows='auto auto 1fr'>
            <Form {...props} />
            </Grid>
        </Overlay>
    );
};

export default Listings;