import React, { useState, useEffect } from "react";
import Layout from '../../../layout';
import { Grid, Card, Button } from "../../../style";
import Header from "./header";
import { useSelector } from "react-redux";
import Chart from '../../../components/chart';
import { useMetricData } from '../../../hooks/metrics';
import useTimeseries from '../../../hooks/timeseries';
import options from './filter-options';
import useGarages from '../../../hooks/garage';
import charts from './chart.json';
import { getInterval  } from '../../../utils/time';
import Filter from "../../../components/filter";
import { TimeSeries } from '../../../model/domain/metrics'

const ts = (timeSeriesQuery: TimeSeries.PartialQuery | any)=>({
    ...timeSeriesQuery,
    interval: getInterval(10)
})
const defaultTimeSeries = ts(charts[0].timeSeriesQuery) as TimeSeries.Query;

const Analytics = ({ switches }: { switches: JSX.Element }) => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const [open, setOpen] = useState(true);

    const { metricDataStatus, refresh } = useMetricData({
        timeseries: defaultTimeSeries,
        eId
    });

    const { timeseries, actions } = useTimeseries(defaultTimeSeries);
    const isPercent = timeseries?.metricType?.split('_')?.includes('percent') || false;
    const { garages: resources } = useGarages({ eId });

    const props = {
        defaultValue: defaultTimeSeries,
        metricDataStatus,
        filterWidth: 350,
        loading: metricDataStatus.loading,
        submit: () => {
            setTimeout(() => setOpen(false), 400);
            refresh(timeseries)
        },
        reset: () => {
            setTimeout(() => setOpen(false), 400);
            actions.refresh(defaultTimeSeries)
            refresh(defaultTimeSeries)
        },
        actions,
        timeseries,
        options: { ...options, resources }
    }

    return (
        <Layout sidebar>
            <Layout.Top >
                {switches}
                <Header />
            </Layout.Top>
            <Layout.Bottom>
                <Grid placeItems="start stretch" padding="1em" height="100%">
                    <Grid cols="3" gridGap="0 1em">
                        <Button
                            placeSelf="end"
                            rounded
                            invert
                            shadow
                            style={{gridColumn: '1/-1'}}
                            minWidth="3em"
                            onClick={()=>setOpen(!open)}
                            faIcon={`fa fa-filter`}
                        />
                        {charts.map((chart: any)=><Chart
                            isPercent={isPercent}
                            className='slide-up'
                            metricDataStatus={metricDataStatus}
                            {...chart}
                        />)}
                    </Grid>
                </Grid>
                <Filter {...props} open={open} toggle={()=>{setOpen(false); actions.refresh(defaultTimeSeries)}} />
            </Layout.Bottom>
        </Layout>
    );
};


export default Analytics;