import React, { useState } from "react";
import Listings from './listings';
import {  Switch } from '../../style';
import Analytics from "./analytics";
import Live from "./live";

enum Pages {
    "ANALYTICS" = "ANALYTICS",
    "LISTINGS" = "LISTINGS",
    "LIVE" = "LIVE"
};

const Events = () => {
    const [activePage, setActivePage] = useState<Pages>(Pages.LISTINGS);
    const switches = <Switch.underlined
        style={{ placeSelf: "start", gridColumn: '1/-1' }}
        items={Object.keys(Pages)}
        onClick={(active: Pages) => setActivePage(active)}
        active={activePage}
    />
    return activePage === Pages.LISTINGS ?
        <Listings switches={switches} /> :
        activePage === Pages.ANALYTICS? 
        <Analytics switches={switches} /> :
        <Live switches={switches} />
};

export default Events;