import React, { useState } from "react";
import { Grid, Button, Text } from "../../../style";
import { primaryColor } from "../../../config";
import img from "../../../assets/announ.svg";
import { InputNumber, Switch } from 'antd';
import { useFormik } from 'formik';
import logger from '../../../utils/logger';

export const Editor = (props: any) => {
    return (
        <Grid customCols="3fr 2fr" width="100%" height="100%" gridGap="0">
            <div style={{ background: primaryColor, height: "100%", padding: "1em", width: "100%" }}>
                <img src={img} style={{ maxWidth: "300px" }} />
            </div>
            <Form {...props} />
        </Grid>
    );
};

const defaultValues = {
    garages: [
        {
            id: '11',
            name: 'Garage 1',
            dynamicPricing: false,
            price: 10
        },
        {
            id: '2',
            name: 'Garage 2',
            dynamicPricing: false,
            price: 15
        },
        {
            id: '2',
            name: 'Garage 2',
            dynamicPricing: false,
            price: 15
        }, {
            id: '2',
            name: 'Garage 2',
            dynamicPricing: false,
            price: 15
        }
    ]
};

const Form = ({ add, close, initialValues = defaultValues }: any) => {
    const [loading, setLoading] = useState(false);
    const { handleSubmit, handleChange, values, setFieldValue } = useFormik({
        initialValues,
        onSubmit(values) {
            submit(values)
        }
    })
    const submit = async (values: any) => {
        try {
            setLoading(true)
            await add(values);
            close();
        } catch (e) {
            logger.log(e)
        } finally {
            setLoading(false)
        }
    }

    const error = false
    const style = {
        padding: ".5em 1em", height: "100%", width: "100%", maxHeight: '500px',
        placeItems: "center stretch", gridGap: ".4em", background: "white"
    }

    return (
        <form onSubmit={handleSubmit} className="grid" style={{ ...style, gridGap: '1em', overflowY: 'auto' }}>
            {
                values.garages.map(({ name }: any, i: number) => (
                    <Grid placeItems="stretch" customCols='auto 1fr' gridGap='0.4em' style={{fontSize: '0.8em'}}>
                        <Text style={{ gridRow: 'span 3', fontSize: '1.2em'}}>{i + 1}.</Text>
                        <Text textAlign="left" placeSelf="start" bold style={{fontSize: '1.2em'}}>{name}</Text>
                        <Grid margin="0" placeSelf="stretch" placeItems="stretch" gridGap="0" customCols="1fr auto">
                            <Text textAlign="left" placeSelf="start" >Dynamic Pricing</Text>
                            <Switch
                                style={{ placeSelf: "end" }}
                                onChange={(checked: boolean) => { }}
                            />
                        </Grid>
                        <Grid placeSelf="stretch" placeItems="stretch" gridGap="0" customCols="1fr auto">
                            <Text textAlign="left" placeSelf="start">Price ($)</Text>
                            <InputNumber
                                min={0}
                                step={0.25}
                                style={{ placeSelf: "start end" }}
                                name={"price"}
                            />
                        </Grid>
                    </Grid>

                ))
            }

            <Grid cols="2" size="0.7em" placeItems={"end stretch"} height='100%' margin='0 0 2em 0'>
                <Button.Normal
                    type="submit"
                    disabled={loading}
                    onClick={() => close()}
                >
                    Cancel
                </Button.Normal>
                {loading ?
                    <Button loading disabled>Loading</Button> :
                    <Button.Normal
                        type="submit"
                        disabled={error}
                        onSubmit={(e: any) => handleSubmit(e)}>
                        Confirm
            </Button.Normal>
                }
            </Grid>
        </form>
    )
}

export default Form;
