import React, { useState, useEffect } from 'react';
import { Header, Card, Text, Button, Grid } from '../style';
import { DatePicker, TimePicker, Divider, Select } from "antd";
import { moment, Moment, FORMAT_TIME } from '../utils/time';
import { Garage } from '../model/domain/garage';
import Label from './label';
import { TimeSeries } from '../model/domain/metrics';

const Filter = ({ open, toggle, filterWidth, loading: loadingTimeSeries, submit, reset, actions, timeseries, options, hideTimeSelection }: any) => {
    const { timeframes, aggregations, metrics, resources } = options;
    const [custom, setCustom] = useState(false);
    const [timeframeLabel, setTimeframeLabel] = useState('')
    const disabledDate = (cur: Moment | null) => cur ? cur > moment() : false;

    const style = { height: "calc(100% - 4em)", width: filterWidth, position: "fixed", zIndex: 5, top: '4em', right: 0 };
    const props = { style: { width: '100%' }, disabled: loadingTimeSeries };
    const dividerStyle = { marginBottom: '.5em', fontSize: '.95em', color: 'gray' }
    const gridProps = hideTimeSelection ? { customCols: '1fr', gridGap: 0, maxHeight: '100%' } : { cols: 2, gridGap: '0 1em' };
    const reverse = {
        points: ['bl', 'tl'], // align dropdown bottom-left to top-left of input element
        offset: [0, -4], // align offset
        overflow: {
            adjustX: 0,
            adjustY: 0, // do not auto flip in y-axis
        },
    }

    const handlePresetChange = (e: any) => {
        if (e.value === null) {
            setCustom(true);
            setTimeframeLabel('Custom');
        }
        else {
            setCustom(false);
            actions.setInterval(e.value);
        }
    }

    useEffect(() => setTimeframeLabel(getDefaultTimeframe(timeframes, timeseries.interval)), [timeseries])

    if (!open) return null;
    return (
        <Card grid shadow style={style} margin="0" placeItems="stretch" gridGap='0' className='slide-in'>
            <Header textAlign="left" className="slide-up">
                <i className="fa fa-times pointer" onClick={() => toggle()}></i>
                <span className="pad">Filters</span>
            </Header>
            {timeframes && <div>
                <Divider style={dividerStyle}>Timeframe </Divider>
                <div className="slide-up">

                    <Text textAlign="left" bold>Presets
                     <Label label={'Interval for the graph data'} />
                    </Text>
                    <Select
                        onChange={(e: any) => handlePresetChange(JSON.parse(e))}
                        {...props}
                        value={timeframeLabel}
                    >
                        {timeframes.map(({ label, enteries }: any) => (
                            <Select.OptGroup label={label}>
                                {enteries.map((entry: any) => (
                                    <Select.Option value={JSON.stringify(entry)}>
                                        {entry.label}
                                    </Select.Option>)
                                )}
                            </Select.OptGroup>))}
                    </Select>
                </div>

                <div className="slide-up" style={{ marginTop: '1em' }}>
                    <Text textAlign="left" bold>From</Text>
                    <Grid {...gridProps} mobileLayout={false}>
                        <DatePicker
                            disabled={!custom}
                            clearIcon={false}
                            disabledDate={disabledDate}
                            style={{ borderRadius: 8, width: '100%' }}
                            onChange={(startTime) => actions.setInterval({ startTime })}
                            value={moment(timeseries.interval.startTime)}
                        />
                        {!hideTimeSelection && <TimePicker
                            disabled={!custom}
                            clearIcon={false}
                            disabledDate={disabledDate}
                            use12Hours
                            format={FORMAT_TIME}
                            minuteStep={15}
                            style={{ borderRadius: 8, width: '100%' }}
                            onChange={(startTime) => actions.setInterval({ startTime })}
                            value={moment(timeseries.interval.startTime)}
                        />}
                    </Grid>
                </div>

                <div className="slide-up" style={{ marginTop: '1em' }}>
                    <Text textAlign="left" bold>
                        To
                     </Text>
                    <Grid  {...gridProps} gridGap='0 1em' mobileLayout={false}>
                        <DatePicker
                            clearIcon={false}
                            disabled={!custom}
                            disabledDate={disabledDate}
                            style={{ borderRadius: 8, width: '100%' }}
                            onChange={(endTime) => actions.setInterval({ endTime })}
                            value={moment(timeseries.interval.endTime)}
                        />

                        {!hideTimeSelection && <TimePicker
                            disabledDate={disabledDate}
                            use12Hours
                            clearIcon={false}
                            format={FORMAT_TIME}
                            disabled={!custom}
                            style={{ borderRadius: 8, width: '100%' }}
                            onChange={(endTime) => actions.setInterval({ endTime })}
                            value={moment(timeseries.interval.endTime)}
                        />}
                    </Grid>
                </div>
            </div>}
            {metrics && <div className="slide-up">
                <Divider style={dividerStyle}>Metric</Divider>
                <Text bold textAlign="left">Select Measurement
                <Label label={'Metric Type'} />
                </Text>
                <Select
                    {...props}
                    placeholder="Select"
                    value={timeseries.metricType}
                    onChange={actions.setMetric}
                >
                    {metrics.map(({ label, value }: any) => (
                        <Select.Option
                            className='capitalize'
                            value={value}
                        >
                            {label}
                        </Select.Option>))
                    }
                </Select>
            </div>}

            {resources && <div className="slide-up">
                <Divider style={dividerStyle}>Resources</Divider>
                <Text bold textAlign="left">Select Garages</Text>
                <Select
                    {...props}
                    mode="multiple"
                    placeholder="All Garages"
                    value={timeseries.resourceIds}
                    onChange={actions.setGarages}
                >
                    {resources.map(({ name, id }: Garage) => <Select.Option value={id}>{name}</Select.Option>)}
                </Select>
            </div>}

            {aggregations && <div className="slide-up">
                <Divider style={dividerStyle}>Aggregation</Divider>
                <Text bold textAlign="left">{aggregations.label}
                    <Label label={'Time period for the data(Min/Max) to be aggregated'} />
                </Text>
                <Select
                    onChange={(e: any) => {
                        const parsedValue = JSON.parse(e);
                        actions.setDayFilter(parsedValue.timeSelection ? undefined : null);
                        actions.setAlignment(parsedValue.value)
                    }}
                    {...props}
                    value={getAlignment(timeseries.alignment, timeseries.dayFilter)}
                    defaultValue={"None"}
                    dropdownAlign={reverse}
                >
                    {aggregations.values.map((props: any) => <Select.Option value={JSON.stringify(props)}>
                        {props.label}
                    </Select.Option>)}
                </Select>
            </div>}

            {timeseries?.dayFilter &&
                <div className="slide-up">
                    <Text bold textAlign="left">{aggregations.label}
                        <Label label={'Time period for the data(Min/Max) to be aggregated'} />
                    </Text>
                    <Grid  {...gridProps} gridGap='0 1em' mobileLayout={false}>
                        <TimePicker
                            use12Hours
                            clearIcon={false}
                            format={FORMAT_TIME}
                            showMinute={false}
                            style={{ borderRadius: 8, width: '100%' }}
                            onChange={(e) => actions.setDayFilter({ hours: [e?.hour(), timeseries.dayFilter.hours[1]] })}
                            value={moment()
                                .set('hour', timeseries.dayFilter.hours[0])
                                .set('minute', 0)}
                            dropdownAlign={reverse}
                        />

                        <TimePicker
                            use12Hours
                            clearIcon={false}
                            showMinute={false}
                            format={FORMAT_TIME}
                            style={{ borderRadius: 8, width: '100%' }}
                            onChange={(e) => actions.setDayFilter({ hours: [timeseries.dayFilter.hours[0], e?.hour()] })}
                            value={moment()
                                .set('hour', timeseries.dayFilter.hours[1])
                                .set('minute', 0)
                            }
                            dropdownAlign={reverse}
                        />

                        <Text
                            bold
                            textAlign="right"
                            style={{ gridColumn: '1/-1' }}
                            width='100%'
                        >
                            {`${timeseries.dayFilter.hours[1] - timeseries.dayFilter.hours[0]} hours`}
                        </Text>
                    </Grid>
                </div>}

            <Grid cols={reset ? 2 : 1} placeItems='stretch' placeSelf='end stretch'>
                <Button
                    shadow
                    onClick={submit}
                    disabled={loadingTimeSeries}
                >
                    Submit
                </Button>
                {reset && <Button
                    onClick={reset}
                    disabled={loadingTimeSeries}
                    noBorder
                    invert
                    shadow
                >
                    Reset
                </Button>}
            </Grid>
        </Card>
    );
}

const getAlignment = (alignment: { alignmentReducer: string, alignmentPeriod: string } | null, dayFilter: TimeSeries.DayFilterQuery) => {
    if (!alignment) return 'None';

    const period = !dayFilter ? 'Daily' : 'Hourly';
    const reducer = alignment.alignmentReducer === 'MIN' ? 'Min' : 'Max';
    return `${period} ${reducer}`;
}

const getDefaultTimeframe = (timeframes: any, interval: any) => {
    if (!timeframes?.startTime || !interval?.startTime || interval?.endTime || !timeframes?.endTime) return;
    const equals = (t1: any, t2: any) => t1.startTime === t2.startTime && t1.endTime === t2.endTime
    const intoInterval = ({ startTime, endTime }: any) => ({
        startTime: typeof startTime === 'string' ? startTime : startTime.toISOString(),
        endTime: typeof endTime === 'string' ? endTime : endTime.toISOString()
    })

    for (let i = 0; i < timeframes.length; i++) {
        const enteries = timeframes[i].enteries;
        for (let j = 0; j < enteries.length; j++) {
            const timeframe = enteries[j]
            if (timeframe?.value && equals(intoInterval(timeframe.value), intoInterval(interval))) {
                return timeframe.label
            }
        }
    }
    for (let i = 0; i < timeframes.length; i++) {
        const enteries = timeframes[i].enteries;
        for (let j = 0; j < enteries.length; j++) {
            const timeframe = enteries[j]
            if (timeframe.defaultValue)
                return timeframe.label;
        }
    }
    return 'None';
}

export default Filter;