import React, { useState, useEffect } from 'react';
import { Grid, Button } from '../../../style';
import Chart from '../../../components/chart';
import { useMetricData } from '../../../hooks/metrics';
import Filter from '../../../components/filter';
import { useSelector } from 'react-redux';
import useTimeseries from '../../../hooks/timeseries';
import options from './filter-options';
import useGarages from '../../../hooks/garage';
import chart from './chart.json';
import { defaultInterval } from '../../../utils/time';
import { TimeSeries } from '../../../model/domain/metrics';

const defaultTimeSeries = {
  ...chart.timeSeriesQuery,
  interval: defaultInterval()
} as TimeSeries.Query;

const Analytics = () => {
  const [open, setOpen] = useState(true);
  const [toggle, setToggle] = useState(false);
  const eId = useSelector(({ user }: any) => user.enterprise.id);
  const { metricDataStatus, refresh, timeseries: currentTimeseries } = useMetricData({
    timeseries: defaultTimeSeries,
    eId
  });

  const { timeseries, actions } = useTimeseries(defaultTimeSeries);
  const isPercent = timeseries?.metricType?.split('_')?.includes('percent') || false;
  const { garages: resources } = useGarages({ eId });

  const props = {
    filterWidth: 350,
    loading: metricDataStatus.loading,
    submit: () => {
      setTimeout(() => setOpen(false), 400);
      refresh(timeseries)
    },
    reset: () => {
      setTimeout(() => setOpen(false), 400);
      actions.refresh(defaultTimeSeries)
      refresh(defaultTimeSeries)
    },
    actions,
    timeseries,
    options: { ...options, resources }
  }

  useEffect(() => {
    if (toggle) {
      refresh(timeseries);
      setToggle(false)
    }
  }, [timeseries]);

  const chart_ = chart as any; //to mute ts error

  return (
    <Grid placeItems="start stretch" padding="1em" height="100%">
      <Grid cols="1" gridGap="0 1em">
        <Chart
          isPercent={isPercent}
          className='slide-up'
          metricDataStatus={metricDataStatus}
          legend={{ label: 'Garages' }}
          filter={
            <span>
              <Button
                invert={timeseries.metricType !== 'garage_occupancy_count'}
                noBorder={timeseries.metricType !== 'garage_occupancy_count'}
                onClick={() => {
                  actions.setMetric('garage_occupancy_count');
                  setToggle(true)
                }}
                faIcon={'fas fa-sort-numeric-up'}
                tooltip='Switch to count'
                minWidth="3em"
                margin='0 0 0 1em'
              />
              <Button
                invert={timeseries.metricType !== 'garage_occupancy_percent'}
                noBorder={timeseries.metricType !== 'garage_occupancy_percent'}
                minWidth="3em"
                onClick={() => {
                  actions.setMetric('garage_occupancy_percent');
                  setToggle(true)
                }
                }
                faIcon={'fas fa-percentage'}
                tooltip='Switch to percent'
              />
              {!open ?
                <Button
                  placeSelf="end"
                  rounded
                  shadow
                  minWidth="3em"
                  onClick={() => setOpen(!open)}
                  faIcon={`fa fa-filter`}
                /> : null}
            </span>
          }
          {...chart_}
        />
        <Filter {...props} open={open} toggle={() => { setOpen(false); actions.refresh(currentTimeseries) }} />
      </Grid>
    </Grid>
  );
}

export default Analytics;