import React from 'react';
import Layout from '../../layout';
import ScoreCards from '../../components/score-card/score-cards';
import { useWidgets } from '../../hooks/widget';
import { useSelector } from 'react-redux';
import { Grid } from '../../style';
import Analytics from './analytics';

const Citations = () => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const { loading, scoreCards } = useWidgets({ eId, screen: "citation" });

    return (
        <Layout sidebar>
            <Grid customCols="1fr auto" width="100%" gridGap="0" placeItems="start" style={{ height: "100%" }}>
                <div style={{ width: "100%", placeSelf: 'start end' }}>
                    <Layout.Top >
                        <div></div>
                        <ScoreCards
                            loading={loading}
                            eId={eId}
                            scoreCards={scoreCards}
                        />
                    </Layout.Top>
                    <Layout.Bottom >
                        <Analytics />
                    </Layout.Bottom>
                </div>
            </Grid>
        </Layout>
    );
}

export default Citations;