import React from 'react';
import { Grid, MenuItem } from '../../../style';
import { List, Popconfirm } from 'antd';
import { Garage } from '../../../model/domain/garage';
import Label from '../../../components/label';

const TableItem = ({ active, select, table, remove, garages, open }: any) => {
    const getGarages = (tableId: string) => garages.filter((g: Garage) => g?.occupancyTableId === tableId, []);
    const associatedGarages = getGarages(table.id);
    return (
        <div style={{ placeSelf: "start stretch" }}>
            <List.Item
                className="pointer"
                onClick={select}
            >
                <MenuItem
                    fullWidth
                    active={active}
                    style={{ margin: "auto" }}
                >
                    <Grid width="100%" color={"inherit"} customCols="auto 1fr auto">

                        <i className="fa fa-table txt-lg" style={{ padding: ".35em" }}></i>
                        <span>
                            {table.title}
                            <Label label={`This Occupancy is associated with: ${
                            associatedGarages ?
                                associatedGarages
                                .map((g: Garage, i: number) => g.name + (i === associatedGarages.length - 1 ? '' : ', ')) : 'N/A'}`} />
                        </span>
                        <span>
                            <Popconfirm
                                style={{ padding: ".4em", color: !active ? "white" : "inherit" }}
                                title="Are you sure"
                                onConfirm={() => remove(table.id)}
                            >
                                <i className="fa fa-trash hoverr" style={{ color: !active ? "white" : "inherit" }} ></i>
                            </Popconfirm>
                            &nbsp;   &nbsp;
                <i className="fa fa-edit hoverr" onClick={open} style={{ color: !active ? "white" : "inherit" }}></i>
                        </span>
                    </Grid>
                </MenuItem>
            </List.Item>
        </div>
    )
};

export default TableItem;