import React from 'react';
import { Grid, Header as Text, List, Empty } from '../../../style';
import useGarages from '../../../hooks/garage';
import { useSelector } from 'react-redux';
import { Garage } from '../../../model/domain/garage';
import { ScoreCard } from '../../../components/score-card/score-card';
import { overflow } from '../../../utils/text';
import { moment } from '../../../utils/time';
import { invertedTxtColor, disabledTxtColor } from '../../../config';
import { Tooltip } from 'antd';

const Header = () => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const { garages, loading } = useGarages({ eId });
    const activeGarages = garages
        .filter((garage: Garage) => garage.status === 'active' && garage.occupancy?.count !== undefined)
        .sort((a, b) => (a.occupancy?.count || 0) - (b.occupancy?.count || 0));


    const percent = (curr: number, total: number) => (((curr / total) * 100) || 0).toFixed(2);
    return (
        <Grid
            cols='5'
            style={{ gridColumn: '1/-1' }}
            width='100%'
            padding='1.5em 1.5em 0 1.5em'
        >
            <Text
                className='grid'
                width='100%'
                margin='0 0 .75em 0'
                style={{ gridColumn: '1/-1', gridTemplateColumns: '1fr auto' }}
                color={invertedTxtColor}
            >
                <Text bold color={'inherit'} textAlign='right' placeSelf='center start' size={3}>
                    <i className='fa fa-bell'></i> &nbsp; SJSU Gradudation Event
                </Text>
                <Text size={4.6} color={'inherit'}>  Updated at: {moment().format('hh:mm A')}</Text>
            </Text>
            <ScoreCard title='Revenue' faIcon='fas fa-money-check-alt' hoverLabel='Revenue' borderRight>
                <span className='txt-lgx'>$6000</span>
            </ScoreCard>

            <ScoreCard title='Transactions' faIcon='fas fa-money-check-alt' hoverLabel='Revenue' borderRight>
                <span className='txt-lgx'>700</span>
            </ScoreCard>
            <ScoreCard title='Garage Occupancies' faIcon='fa fa-car' hoverLabel='Occupancy Count' span={2}>
                <Grid placeSelf='start' >
                    <Grid
                        customCols={loading ? '1fr' : '1fr  minmax(15em, auto)'}
                        placeItems='stretch'
                        width='100%'
                        color='white'
                        gridGap='.2em 1.5em'
                    >
                        <List
                            loading={loading}
                            empty={
                                <div style={{ gridColumn: '1/-1' }}>
                                    <Empty text={'No Garage Occupancy'} color="white" style={{ width: '7em', margin: '0 auto' }} />
                                </div>
                            }
                            pagination={{ items: 2 }}
                            list={activeGarages}
                            map={(({ name }: Garage, index: number) => {
                                return (
                                    <>

                                        <span className='txt-overflow'>{overflow(name, 18)}</span>
                                        <ScoreCard.PercentBar
                                            status
                                            percent={66.67}
                                            text={66.67 + "%"}
                                        />
                                        <Grid
                                            cols='2'
                                            style={{ gridColumn: '1/-1', marginBottom: '1em' }}
                                            width='100%'
                                            placeItems='stretch'
                                        >

                                            <span className='txt-left bold'>
                                                Expected Staff: 20
                                            </span>

                                            <span className='txt-right bold'>
                                                Available Spaces: 20/30
                                        </span>
                                        </Grid>

                                    </>
                                )
                            })}
                        />
                    </Grid>
                </Grid>
            </ScoreCard>

        </Grid>);
}

export default Header;